.btn{
    background: transparent;
    color: $white;
    padding: 11px 14px;
    border: $highlightBlack 2px solid;
    border-radius: 9px;
    font-size: 0.8em;
    transition: border 0.5s ease-out;
}

.full-width{
    width: 100%;
}

.btn:hover:not(.fill){
    border: $gray 2px solid;
}

.fill{
    background: $primaryBlue;
    color:$white;
}

.outline{
    border: $white 1px solid;
}

.disabled{
    opacity: 0.5;
}


.toggle-container{
    height:calc(100% - 398px);
    display: flex;
    flex-direction: column;
    justify-content: flex-end;

    .theme-toggle{
        width: 200px;
        height: 37px;
        background: $black;
        margin:25px auto;
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-radius: 40px;
        
    
        button{
            width:45%;
            height: 85%;
            border-radius: 40px;
            color: $highlightBlack;
            border:none;
            font-weight: 600;
        }
    
        .selected-theme{
            background:$highlightBlack;
            border-top: solid $gray 1px;
            border-bottom: solid $black 1px;
            color: $white;
        }
    }

}
