@import './colors.scss';
@import './themes.scss';

*{
    margin:0;
    padding:0;
    box-sizing:border-box;
    text-decoration: none;
    list-style-type:none; 
}    

h1,h2,h3,h4,h5,h6,a{
    font-family: 'Ubuntu' , sans-serif;
}

p,li,a,label{
    font-family: 'Open Sans' , sans-serif;
}

h3{
    font-size: 2.5em;
}

.light{
    opacity: 0.5;
}

.flex{
    display: flex;
    align-items: center;
}

.section{
    background: $offBlack;
    padding: 20px;
    border-radius: 9px;

    .heading-with-bar{
        padding-bottom: 20px;
    }
}


// these are used in the figures - change
