.green , .red , .white{
    font-weight: 600;
}

.stamp{
    font-size: 0.8em;
    padding: 2px 8px;
    border-radius:5px;
}

.green{
    background: rgba($secondaryGreen, 0.10);
    color:$primaryGreen;
}

.red{
    background: rgba($secondaryRed, 0.10);
    color:$primaryRed;
}

.white{
    background: rgba($white, 0.10);
    color:$white;
}

.like{
    background: $primaryRed;
}

.chat{
    background: $primaryBlue;
}


