.sidebar-container{
    display: flex;
    flex-direction: column;
    width: 270px;
    background:$offBlack;
    height: 100vh;
    position: fixed;
    left: 0;
    top: 0;
    

    a{
        border-top: solid transparent 1px;
        border-bottom: solid transparent 1px;
    }


    li{
        border-top: solid transparent 1px;
        border-bottom: solid transparent 1px;
    }

    .logo-container{
        padding:20px 30px;   

        .logo{
            padding: 20px;
            width: 10px;
            border-radius: 100px;
            background: $white;
            height: 10px;
            position: relative;

            svg{
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                margin: auto;
            }
        }
    }


    // Side bar dropdown

    .sidebar-nav{

        padding: 0px 10px;


        >li{
            position: relative;
            font-size: 1.2em;
            border-radius: 7px;
            margin-bottom: 8px;
            // border: solid transparent 1px;
            overflow: hidden;

            >a{
                padding: 10px 20px;
                color:$gray;
                display:flex;
                align-items: center;
                font-weight: 500;
                font-size: 0.8em;
                justify-content: space-between;
                border-radius: 11px;

                svg{
                    min-width: 20px;
                    fill:$gray;
                    margin-right: 10px; 
                }

                &:hover{
                    background: $highlightBlack;
                    border-top: solid $gray 1px;
                    border-bottom: solid $black 1px;
    
                    svg{
                        min-width: 20px;
                        fill:$white;
                        margin-right: 10px; 
                    }
    
                }
            }


            .dropdown{
                border: none;
                position: relative;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                background: transparent;
                font-size: 0.8em;
                padding:0px 46px;
                height: 0px;
                opacity: 0;
                z-index: 1;

                >li{
                    background: $offBlack;
                    margin: 5px 0px ;
                    padding: 5px 8px;
                    position: relative;

                    a{
                        color: $gray;
                        transition: color 0.1s ease-out;
                    }

                   &:hover a{
                        color: $white;
                    }
                    
                    &:not(:last-child):after{
                        position: absolute;
                        content: '';
                        height: 6px;
                        width: 7px;
                        top:0;
                        left: -16px;
                        bottom:0;
                        border-left:solid $highlightBlack 2px;
                        border-bottom:solid $highlightBlack 2px;
                        border-bottom-left-radius: 5px;
                        margin: auto;
                    }

                    &:last-child{
                        margin-bottom: 0;
                    }

                    &:hover{
                        background: $highlightBlack;
                        border-top: solid $gray 1px;
                        border-bottom: solid $black 1px;
                        color: $white;
                        padding: 5px 8px;
                        border-radius: 6px;
                    }
                }

                &::after{
                    content:'';
                    position: absolute;
                    background: $offBlack;
                    top:0;
                    left: 0;
                    height: 90%;
                    width: 8px;
                    left:30px;
                    border-left:solid $highlightBlack 2px;
                    border-bottom:solid $highlightBlack 2px;
                    border-bottom-left-radius: 5px;
                    z-index: -1;
                }
            }
        }
    }
}