fieldset{
    border:hidden;
}
.add-products{

    .dropdown-container{
        width: 100%;

        .drop{
            width: 100%;
            left: 0;
        }
    }

    input:not(input[type="checkbox"]){
        width: 100%;
    }
    
    form{
        display: flex;
        flex-direction: column;
        width: 100%;

        .section:not(:nth-child(5)){
            margin-bottom:20px;
        }

        .section:nth-child(5){
            margin-bottom: 100px !important;
        }

        .field{
            display: flex;
            flex-direction: column;
            margin-bottom: 20px;

            .values{
                display: flex;
                justify-content: space-between;
                flex-wrap: wrap;

                >input{
                    width: 49.7%;
                    min-width: 300px;
                    margin-bottom: 10px;
                }

            }

            .field-title{
                font-weight: 600;
                font-size:1.2em;
                margin-bottom:16px;

                &::after{
                    content:'\f05a';
                    font-family: "Font Awesome 5 Free";
                    font-weight: 900;
                    color: $white;
                    font-size: 15px;
                    margin-left: 5px;
                }
            }
        }
    }
    
    .category{
        .checkboxes{
            width: 100%;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;

            .checkbox{
                width: calc(100% / 3);
                display: flex;
                align-items: center;
                margin-bottom: 15px;
                 
                span{
                    margin: none;
                    margin-left: 10px;
                    font-weight: 600;
                    font-size: 1.3em;
                }
            }
        }
    }

    
    .add-file{
        width: 100%;
        height: 300px;
        background: rgba($white , 0.05);
        padding:10px;
        border-radius:10px;
        position: relative;
    }

    .add-file-button{
        width: fit-content;
        border-radius:9px;
        height:fit-content;
        padding: 13px;
        background:$white;
        border:solid $gray 2px;
        box-shadow: -3px 1px 25px $offBlack;
        position:absolute;
        top:0;
        bottom:0;
        left:0;
        right:0;
        margin:auto;
        display: flex;

        input[type="file"]{
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            opacity: 0;
        }

        h4{
            color: $black;
        }

        &::before{
            content:'\f093';
            font-family: "Font Awesome 5 Free";
            font-weight: 900;
            color: $offBlack;
            margin-right:10px;
        }
    }
    

    .allow-customer{
        display: flex;
        justify-content:space-between;

        .switch-container{
            background: pink;
            width: 60px;
            height: 25px;
            border-radius: 25px;
            position: relative;

            &::after{
                position: absolute;
                content:'';
                left: 0;
                height: 100%;
                width: 25px;
                background: blue;
                border-radius: 100%;
            }
        }
    }

    
    .price-values{
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        >div{
            width: 49%;
            min-width: 300px;
            >div{
                margin-top:10px;
            }
        }
    }

    .price-value-input{
        position: relative;

        input{
            padding-left: 60px;
        }

        &::after{
            content:'';
            position: absolute;
            width: 40px;
            height: 100%;
            background: rgba($white , 0.05);
            border-radius:10px 0px 0px  10px;
            left:0;
        }
    }

    .tag-container{
        .product-tags{
            width: 100%;
            height: 42px;
            border-radius: 9px;
            border:solid 2px $highlightBlack;
            display: flex;

            .tag-item{
                width:fit-content;
                margin-right:10px;
                height: 100%;
                border-radius:9px;
                background: $primaryBlue;
                display: flex;
                align-items: center;
                padding:10px 20px;
                justify-content: space-between;

                div::after{
                    content:'\f00d';
                    font-family: "Font Awesome 5 Free";
                    font-weight: 900;
                    color: $white;
                    padding-left: 10px;
                }

                p{
                    white-space: nowrap;
                }
            }

            .tag-input{
                border: none;

                &:focus{
                    outline: none;
                }
            }
        }
    }

    .form-actions{
        background: $offBlack;
        padding: 20px  40px;
        position: absolute;
        border-left: solid 2px #111315;
        bottom: 0;
        width: 100%;
        left: 0;
        display: flex;
        justify-content: space-between;
        align-items: center;

        button{
            width: fit-content;
        }
    }
}



