 .top-blocks{
  display: flex;
  justify-content: space-between;
  width:100%;
  overflow: hidden;
  // flex-wrap: wrap;
  // flex-shrink: 1;

    .top-blocks-content{
      width: 30%;
      // flex-grow: 1;
      min-width: 250px;
      border-radius: 9px;
      padding: 30px;
      margin: 10px;
      display: flex;
      justify-content: space-between;
      align-items: center;

      .left-content{
        >*:not(:nth-child(2)){
          margin-bottom:10px;
        }
        .icon{
          width: 40px;
          height:40px;
          display: flex;
          justify-content: center;
          align-items: center;
          background: $white;
          border-radius:100%;

          svg{
            color: $black;
          }
        }
        .block-title{
          h5{
            display: flex;
            align-items: center;
            .info::after{
              content:'\f05a';
              font-family: "Font Awesome 5 Free";
              font-weight: 900;
              color: $white;
              font-size: 15px;
              margin-left: 5px;
            }
          }
        }
        .trend-stat{
          background: rgba($black , 0.90);
          display: inline-block;
          padding:2px 7px;
          border-radius: 5px;

          h5{
            display: flex;

            .up{
              margin-right:5px;
              svg{
                margin-left: 0;
              }
            }
          }
        }

      }
    }

    .top-blocks-content:nth-child(even){
      background: rgba($secondaryGreen, 0.10);
    }

    .top-blocks-content:nth-child(odd){
      background: rgba($secondaryBlue, 0.10)
    }

}


th , .table-date{
  font-weight: 600;
  color: $gray;
}

.table-data{
  display: flex;
  align-items: center;
  font-weight: 600;
}

.main-points{
  padding: 7px;
  border-radius: 5px;
  background: $primaryBlue;
  line-height: 11.3px;
  margin-right: 5px;
}

th, tr:not(:last-child){
  border-bottom: 1px solid $highlightBlack;
}

table{
  border-collapse: collapse;
  border-spacing: 0.5rem;
  width: 100%;
  min-height: 230px;
  min-width: 620px;
}

td, th {
  text-align: left;
}

.up , .down{
  display: flex;
  align-items: center;
}

.up{
  color:$primaryGreen;

  svg{
      margin-right: 5px;
      margin-left: 10px;
  }
}

.down{
  color:$primaryRed;

  svg{
      margin-right: 5px;
      margin-left: 10px;
      transform: rotate(180deg);
  }
}

.product-activity{
  flex-wrap: wrap;
  display: flex;
  justify-content: space-between;
  flex-shrink: 1;

  .section{
    flex-grow: 1;
    margin-bottom: 10px;
  }

  .stats-table{
    width:calc(100% - 500px);
    min-width: fit-content;
    margin-right: 10px;
  }

  .product-views{
    width: 350px;

    .graph-container{
      margin:0;
      margin-bottom: 30px;

      li{
        border:none;
      }

      .bar-increase{
        border-radius:5px;
      }
    }

  }
}

.product-activity-container{

  h5{
    line-height: 1.4;
    margin-bottom: 2px;
  }

  form{

    td:not(:first-child), th:not(:first-child) {
      width: calc(100%/7);
    }

  }

  .flex{
    >div{
      margin-right: 10px;
    }
  }

  .increase-bar , .decrease-bar{
    width: 50px;
    height: 12px;
    border-radius: 2px;
  }

  .increase-bar{
    background: $primaryGreen;
  }

  .decrease-bar{
    background: $primaryRed;
  }

  .productDescription{
    .product{
      display: flex;
      .image{
        background: pink;
        background-image: url('https://images.unsplash.com/photo-1513364776144-60967b0f800f?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1171&q=80');
        background-size: cover;
        background-position:top;
        width:100px;
        height: auto;
        min-height: 50px;
        max-height: 100px;
        border-radius: 9px;
      }
    }
  }

  .pagination{
    display: flex;
    width: 100%;
    justify-content: center;
    border-top:1px solid $highlightBlack;
    padding-top: 30px;

    .previous{
      margin-right: 10px;
      svg{
        transform: rotate(-90deg);
      }
    }
    .next{
      svg{
        transform: rotate(90deg);
      }
    }

    svg{
      width: 30px;
      height: 30px;
    }
  }
}