:root {
    --text-light:#fcfcfc;
    --light-background:#fcfcfc;
    --light-gray:#f4f4f4;
    --gray: #e2e2e3;
    --text-dark:#111315;
    --background-dark:#111315; 
    --dark-boxes: #1a1d1f;
    --dark-highlight:#272b30;
    --link:#2a85ff;
}
  
.light-mode{
    background: var(--light-gray);
    p, h1 ,h2 ,h3 , h4 , li , .block-title , .message ,.product-grid-container ,.load-more ,table, .info::after , a , .last-saved, button , .form-block , input::placeholder , input , .price , .checkbox span , .field-title , .field-title::after{
        color: var(--text-dark) !important;
    }

    .tag-item p , .fill {
        color: var(--text-light) !important;
    }

    input , textarea , .drop , .dropdown , .product-tags , .outline , .load-more{
        border-color: var(--gray) !important;
    }


    .add-file , .price-value-input::after{
        background: rgba(#e2e2e3 , 0.30) !important;
    }

    .add-file-button{
        border-color: var(--gray) !important;
        box-shadow: -3px 1px 25px var(--gray) !important;
    }

    hr{
        border: solid 2px  var(--light-gray) !important;
    }


    svg{
        fill:gray;
    }

    .create-btn {
        svg{
            fill: var(--light-background)
        }
    }

    .btn , .list-headings ,.dropblock, .top-bar , .numbers li , .chart .bars li:not(:last-child) ,.description , .notification-dropdown , .add-products .form-actions{
        border-color:var(--light-gray) !important;
    }

    .notification-dropdown .triangle  , .dropblock:after{
        border-bottom-color: var(--light-background) !important;
    }

    .pagination{
        border-top-color: var(--light-gray) !important;
    }

    tr , thead , table , th{
        border-color: var(--light-gray) !important;
    }

    .search-box{
        background: var(--light-gray) !important;
    }

    .search-box-cont input::placeholder{
        color:var(--text-dark) !important;
    }

    .shortcut{
        background: var(--light-background) !important;
        span{
            color: var(--text-dark);
        }
    }

    .section  , .dropblock ,.notification-dropdown , .form-block , .top-bar , .sidebar-container , .point , .dropdown:after , .dropdown li , .dropdown-container .clicked , .views-popup , .add-products .form-actions{
        background: var(--light-background) !important;
    }

    .figures{
        background: var(--light-gray) !important;

        .highlighted{
            border-top: solid var(--light-gray) 1px;
            border-bottom: solid var(--light-gray) 1px;
            box-shadow: 0px 0px 2px var(--light-gray);
        }

        .highlighted::after{
            background: var(--light-background);
        }
    }

    .theme-toggle{
        background: var(--light-gray) !important;

        .selected-theme{
            background: var(--light-background) !important;
            color: var(--text-dark) !important;
        }

        button{
            background: transparent;
        }
    }

    .dropdown::after , .dropdown li::after{
        border-color:var(--light-gray) !important;
    }

    .sidebar-nav > li > a:hover{
        background: var(--light-gray) !important;
        border-top: solid var(--light-gray) 1px !important;
        border-bottom: solid var(--light-gray) 1px !important;

        svg{
            fill: gray !important;
        }
    }

    .drop{
        li:hover{
            background: var(--light-gray) !important;
            border-top: solid var(--light-gray) 1px !important;
            border-bottom: solid var(--light-gray) 1px !important;
        }
    }

    .logo , .left-content .icon{
        background:var(--background-dark) !important;
        svg{
            fill: var(--text-light);   
        }
    }
    
    .time{
        .description{
            color: var(--text-dark);
        }
    }

    transition: all 0.2s cubic-bezier(.62,.01,1,1);
}

.dark-mode{
    background: var(--background-dark);

    p, h1 ,h2 ,h3 , h4 , li , a , button{
        color: var(--text-light);
    }

    .theme-toggle{
        button{
            background: transparent;
        }
    }

    .views-popup{
        background: var(--background-dark) !important;
    }

    .logo{
        background:var(--light-background) !important;
        svg{
            fill: var(--text-dark);   
        }
    }

    transition: all 0.2s cubic-bezier(.62,.01,1,1);
}