
td{
    height: 100px;
}

table{
    border-collapse:separate;
    border-spacing: 15px;
}

.heading{
    &-checkbox{
        width: 5%;
    }

    &-product{
        width: 30%;
    }

    &-price{
        width: 20%;
    }

    &-last-edit{
        width: 30%;
    }

    &-actions{
        width:15%;
    }
}

.product{
    &-image{
        display:flex;
        align-items: center;

        .image{
            width:100px;
            height: 100%;
            background: pink;
            border-radius: 9px;
            background-image: url('https://images.unsplash.com/photo-1509828945144-552b3b1a968d?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=687&q=80');
            background-size: cover;
        }
    }

    // &-price{
        
    // }

    // &-last-edit-timestamp{
        
    // }

    &-row-actions{
       >div{
          display:flex;

          >div{
            padding:10px;
          }
        }
    }
}

table{
    .product-row-actions{
        >div{
            display: none;
        }

        &:hover{
            >div{
                display: flex;
            }
        }
    }
}

.icon-container{
    width:35px;
    height:35px;
    background:$black;
    border-radius:100%;
    margin: 10px;
    position: relative;

    &:nth-child(1)::after{
        content: '\f133';
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        color: $white;
        position:absolute;
        font-size: 13px;
        top: 10px;
        left: 13px;
        bottom: 0;
        right: 0;
    }

    &:nth-child(2)::after{
        content: '\f303';
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        color: $white;
        position:absolute;
        font-size: 13px;
        top: 10px;
        left: 13px;
        bottom: 0;
        right: 0;
    }

    &:nth-child(3)::after{
        content: '\f1f8';
        font-family: "Font Awesome 5 Free";
        font-weight: 900;
        color: $white;
        position:absolute;
        font-size: 13px;
        top: 10px;
        left: 13px;
        bottom: 0;
        right: 0;
    }
}


.products-grid{
    display: flex;
    flex-wrap: wrap;

    .product-grid-container{
        padding: 10px;
        width:calc(100% / 3);
        min-width: 300px;

        .product-grid-image{
            width: 100%;
            border-radius: 9px;
            margin-bottom: 10px;
            height: 200px;
            position: relative;
            background: pink;
            background-image: url('https://images.unsplash.com/photo-1509828945144-552b3b1a968d?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=687&q=80');
            background-size: cover;
            background-position: center;
            
            &:hover .grid-hover-actions{
                display: flex;
            }
        }
    
        .product-grid-title-and-price{
            display: flex ;
            margin-bottom: 5px;
    
            .product-title{
                width: 200px;
            }
    
            .product-price{
                width: calc(100% - 200px);
                display: flex;
                justify-content: flex-end;
            }
        }
    
        .product-grid-timestamp{
            opacity: 0.5;
        }
    }
}

.grid-hover-actions{
    position: absolute;
    display: none;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    
    .product-row-actions{
        position: absolute;
        z-index: 2;
    }
    
    &::after{
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        background:rgba($white , 0.50);
        border-radius: 9px;
        z-index: 1;
    }
}

.load-more-container{
    width:100%;
    display: flex;
    justify-content: center;

    .load-more{
        border:2px solid $gray;
        display: inline-block;
        padding: 10px;
        border-radius: 9px;
        margin: auto;
        
        &::before{
            content: '\f110';
            font-family: "Font Awesome 5 Free";
            font-weight: 900;
            margin-right: 5px;
        }
    }
}