@keyframes anidown{
    0%{
        transform: translateY(-100vh);
    }

    95%{
        transform: translateY(4vh)
    }

    100%{
        transform: translateY(0vh)
    }
}

.clicked{
    animation: popout forwards 0.6s ease-in-out;
    display: block !important;
}

@keyframes popout{
    0%{
        transform:scale(0);
    }

    80%{
        transform:scale(1.2);
    }

    100%{
        transform:scale(1);
    }
}

.highlighted{
    position: relative;
    overflow: hidden;
    border-top: solid #474e53 1px;
    border-bottom: solid #1a1c1e 1px;
    box-shadow: 0px 0px 2px $black;

    >div{
        position: relative;
        z-index: 1;
    }
}

.highlighted::after{
    content: '';
    position: absolute;
    padding: 5px;
    top:0;
    left: 0;
    height: 100%;
    width: 100%;
    background: $highlightBlack;
}

.rotate{
    transform:rotate(180deg);
    transition: all 0.5s ease-out;
}

.bak{
    transform:rotate(0deg);
    transition: all 0.5s ease-out;
}

@keyframes slide{
    0%{
        transform: translateX(-100%);
    }
    100%{
        transform: translateX(0%)
    }
}

.drp-nav{
    transition:all 0.5s ease-out;
    display: block;
    animation: drop 0.5s forwards ease-out;
}

@keyframes drop{
    0%{
        height:0%;
    }

    100%{
        opacity: 1;
        height: 100%;
    }
}

.bar-increase{
    animation: increase 0.9s forwards ease-in;
}

@keyframes increase{
    0%{
        height: 0%
    }

    100%{
        height: 100%;
    }
}

// dropdown

.dropdown-container{
    position: relative;
    width: fit-content;
    color:$white;
    
    .dropdown{

        >li{
            display: flex;
            justify-content: space-between;

            >div{
                margin-left: 10px;
            }
        }

        .drop{
            display: none;
        }

        .clicked{
            display: block;
            position: absolute;
            bottom: -260%;
            // width: 100%;
            left: -20px;
            width: 150px;
            background: $offBlack;
            z-index: 2;
             
            li{
                font-size: 0.8rem;
                padding: 7px 12px;
                border-radius: 9px;
                transition: background 0.2s ease-out;
            }

            li:hover{
                background: $highlightBlack;
            }
        }
    }
}

.dropdown , .drop{
    background: transparent;
    color: $white;
    padding: 11px 14px;
    border: $highlightBlack 2px solid;
    border-radius: 9px;
    font-size: 0.8em;
    transition: border 0.5s ease-out;
}

