  font-family: "Font Awesome 5 Free";
  font-weight: 900;
  font-size: 10px;
  width: 10px;
  height: 10px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  display: none;
}

