  position: absolute;
  content: "";
  height: 6px;
  width: 7px;
  top: 0;
  left: -16px;
  bottom: 0;
  border-left: solid #272b30 2px;
  border-bottom: solid #272b30 2px;
  border-bottom-left-radius: 5px;
