/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 992px) and (min-width: 300px) {
  .sidebar-container {
    display: none;
  }

  .main-body-container {
    width: 100%;
  }
  .main-body-container .main-content .main-content-container {
    padding: 20px 20px;
  }
  .main-body-container .top-bar {
    width: 100%;
  }
  .main-body-container .top-bar .create-btn {
    display: none;
  }
  .main-body-container .top-bar .top-bar-inner-cont .top-bar-side-nav {
    justify-content: space-between;
    width: calc(100% - 200px);
    max-width: none;
    min-width: none;
  }
  .main-body-container .top-bar .top-bar-inner-cont .hamburger-icon {
    display: flex;
  }
  .main-body-container .top-bar .top-bar-inner-cont .search-box-cont {
    display: none;
  }
}
/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
  .hamburger-icon {
    display: none;
  }
}
/* Medium devices (landscape tablets, 768px and up) */
/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
  .sidebar-container {
    display: flex;
  }
}
/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {
  .sidebar-container {
    display: flex;
  }
}

